.growth-card {
	display: flex;
	flex-direction: column;
	margin: 2rem 0;
	width: 100%;

	&:nth-child(1) {
		margin: 0;
	}

	&:nth-last-child(1) {
		margin-top: 2rem;
		margin-bottom: 0;
	}

	@media (min-width: 576px) {
		width: auto;
	}

	@media (min-width: 768px) {
		margin: 0 2rem;

		&:nth-child(1) {
			margin-right: 2rem;
		}

		&:nth-last-child(1) {
			margin-left: 2rem;
			margin-top: 0;
		}
	}

    &__header-box{
        display: flex;
		justify-content: space-between;
		align-items: center;
    }

    &__text {
		font-family: "roboto", sans-serif;
		font-size: var(--small-size);
		margin-right: 2rem;
		padding: 1rem;
		border-radius: 6px;

		&--purple {
			background-color: var(--light-purple);
		}

		&--green {
			background-color: var(--light-green);
		}

		@media (min-width: 768px) {
			font-size: var(--medium-size);
		}

		@media (min-width: 992px) {
			font-size: var(--normal-size);
		}
	}

	&__number-count {
		font-family: "roboto", sans-serif;
		font-weight: bold;
		font-size: var(--big-size);

		@media (min-width: 768px) {
			font-size: var(--large-size);
		}
	}

	&__description {
		margin-top: 2rem;
		font-size: var(--small-size);

		&--percentage {
			color: var(--light-green);
			margin-right: 0.5rem;
		}

		@media (min-width: 768px) {
			font-size: var(--medium-size);
		}

		@media (min-width: 992px) {
			width: 60%;
			text-align: right;
			align-self: end;
			font-size: var(--normal-size);
		}
	}
}